<template>
  <v-app>
    <v-navigation-drawer absolute permanent expand-on-hover right app class="float-left text-left">
      <v-treeview
        hoverable
        :items="items"
        activatable
        color="#e9e9e9"
      >
        <template v-slot:label="{ item }">
          <div 
            @click="onOpen(item)" 
            :style="`cursor: pointer`" 
            :class="`${(item.type == 'modbus-master' || item.type == 'debug-message') ? 'font-weight-bold' : '' }`"
          >
            {{item.name}}
          </div>
        </template>
      </v-treeview>
    </v-navigation-drawer>
    <v-main>
      <v-container fluid>
        <router-view class="table-view"></router-view>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  import axios from "../../axios";
  import DebugMessages from '../Common/DebugMessages.vue';
  export default {
    components: {
      DebugMessages
    },
    data: () => ({
      topic: 'rubix/points/debug',
      tab: 0,
    }),
    computed: {
      items () {
        return [
          { id: 1, name: 'Data', children: this.networksData, type:'modbus-master' },
          { id: 2, name: 'Debug Messages', type:'debug-message' },
        ]
      },
      networksData () {
        const { mbNetworks } = this.$store.state;
        return this.getTreeViewData(mbNetworks);
      },
    },
    beforeRouteLeave (to, from, next) {
      axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
        next();
      });
    },
    mounted() {
      if(this.$router.currentRoute.name != 'modbus-networks'){
        this.getNetworks();
      }
    },
    methods: {
      getNetworks() {
        axios.get( 'point-server/network?with_children=true' )
          .then((data) => {
            this.$store.commit('changeMbNetworksData', data);
          });
      },
      onOpen(item){
        switch (item.type) {
          case "modbus-master":
            this.$router.push({ path: `/modbus` }).catch(err => {});
            break;
          case "debug-message":
            this.$router.push({ path: `/modbus/debug-message` }).catch(err => {});
            break;
          case "network":
            this.$router.push({ path: `/modbus/networks/${item.id}/devices` }).catch(err => {})
            break;
          case "device":
            this.$router.push({ path: `/modbus/devices/${item.id}/points` }).catch(err => {});
            break;
        }
      },
      getTreeViewData(networksData){
        if(!networksData || networksData && networksData.length == 0){
          return;
        }
        const result = networksData.map(nw=>{
          if(nw.devices && nw.devices.length > 0){
            const devices = [];
            nw.devices.forEach(de => {
              const device = {id: de.uuid, name: de.name, type:'device'};
              devices.push(device);
            });
            return {id: nw.uuid, name: nw.name, children: devices, type:'network'};
          }
          else{
            return {id: nw.uuid, name: nw.name, type:'network'};
          }
        });
        return result;
      },

    },
  };
</script>
<style >
.table-view{
  margin-top: -12px;
  margin-left: -12px;
}
</style>

