<template>
  <v-card>
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <v-form :name="formName" v-model="valid" ref="form" lazy-validation>
        <v-text-field label="Username" v-model="username" :rules="[rules.required]"></v-text-field>
        <v-text-field
          :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="showPassword = !showPassword"
          :type="showPassword ? 'text' : 'password'"
          label="Password"
          v-model="password"
          :rules="isEdit ? updatePasswordRules : [rules.required]"
        ></v-text-field>
      </v-form>
      <span v-if="errorMessage" class="error-message">{{ errorMessage }}</span>
    </v-card-text>
    <v-card-actions class="justify-end pa-4">
      <v-btn v-if="isEdit" @click="onCancel">Cancel</v-btn>
      <v-btn color="blue darken-2" @click="onSave">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import validation from '../../common/validation';
import * as systemLoginService from '../../services/systemLoginService';
export default {
  name: 'system-service-login-form',
  props: ['serviceName', 'fetchConfigStatus', 'formName', 'title', 'isEdit', 'formDetail', 'onCancel'],
  data() {
    return {
      valid: false,
      errorMessage: '',
      isConfigured: false,
      showPassword: false,
      username: '',
      password: '',
      rules: {
        required: validation.required,
      },
      updatePasswordRules: [
        validation.required,
        // value => !!value.match('(?=.*?[A-Z])') || 'At least 1 Uppercase',
        // value => !!value.match('(?=.*?[0-9])') || 'At least 1 Number',
        // value => value.length >= 8 || 'At least 8 charaters',
      ],
    };
  },
  mounted() {
    if (this.isEdit) {
      this.username = this.formDetail.username;
    }
  },
  methods: {
    onSave() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.errorMessage = '';
      if (this.isEdit) {
        this.onUpdate();
      } else {
        this.onCreate();
      }
    },
    onCreate() {
      systemLoginService
        .integrateSystemService(this.serviceName, { username: this.username, password: this.password })
        .then(data => {
          this.fetchConfigStatus();
        })
        .catch(err => {
          if (err && err.message && typeof err.message === 'string') {
            this.errorMessage = err.message;
          }
        });
    },
    onUpdate() {
      systemLoginService
        .updateUserPassword(this.serviceName, { username: this.username, password: this.password })
        .then(data => {
          this.fetchConfigStatus();
        })
        .catch(err => {
          if (err && err.message && typeof err.message === 'string') {
            this.errorMessage = err.message;
          }
        });
    },
  },
};
</script>
