  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    tags(val) {
        this.setTagStr(val);
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getAllPoints();
    
    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllPoints();
      }, this.refreshTime);
    }
  },
  methods: {
    getAllPoints() {
      axios.get(`point-server/generic/network/${this.$route.params.id}?with_children=true&points=true`).then(data => {
        this.network = data;
        this.getDataTable(data.devices);
      });
    },
    getDataTable(devices){
      this.allPoints = [];
      devices.forEach(device=>{
        if(device.points.length > 0){
          const pointsPerDevice = device.points.map(point=>{
            const keys = Object.keys(point.point_store);
            if (keys && keys.length > 0) {
              keys.forEach(key => {
                point = { ...point, [key]: point.point_store[key], category: device.name };
              });
            }
            delete point.point_store;
            delete point.point_uuid;
            return point;
          });
          this.allPoints = this.allPoints.concat(pointsPerDevice);
        }
      });
    },
    onChangeTag(data, key, index) {
        this.tags[index][key] = data;
        this.setTagStr(this.tags);
    },
    setTagStr(tags) {
        const result = {};
        for (const tag of tags) {
            if (tag.key && tag.value) {
                result[tag.key] = tag.value;
            }
        }
        this.editAll[this.editField] = JSON.stringify({...result});
        this.editedItem.tags = JSON.stringify({...result});
    },
    // getDir(){
    //   axios.get(`point-server/generic/network/${this.device.network_uuid}`)
    //     .then((data)  => {
    //       this.dirs= [{text: '..', link: '/rubix-driver/networks'}],
    //       this.dirs.push({text: data.name, link: `/rubix-driver/networks/${data.uuid}/devices`});
    //       this.dirs.push({text: this.device.name, disabled: true});
    //     });
    // },
    // clickBreadcrumb(dir) {
    //   if(!dir.disabled){
    //     this.$router.push({path: dir.link});
    //   }
    // },
    editItem(item) {
      this.editedIndex = this.allPoints.findIndex(p=>p.uuid == item.uuid);
      this.pointsOfDevice = this.allPoints.filter(point=>point.device_uuid === item.device_uuid);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
          const tags = JSON.parse(item.tags);
          for (const tag in tags) {
              arrTags.push({key: tag, value: tags[tag]});
          }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.allPoints.indexOf(this.currentItem);
      this.allPoints.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      let value = item[fieldName];
      let points = _cloneDeep(this.pointsOfDevice);
      const index = points.findIndex(p=>p.uuid == item.uuid);
      points.splice(index, 1);
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    updatePoint(data) {
      axios.put(`point-server/generic/point/${data.uuid}`, data )
        .then((response) => {
          const index = this.allPoints.findIndex(n => n.uuid == response.uuid);
          Object.assign(this.allPoints[index], response);
          this.close();
          this.$toasted.show('update success');
        })
        .catch((error) => { this.$toasted.show('update fail', {type: 'error'}); });
    },
    backgroundColor(item) {
      let classColor = '';
      if(item.fault){
        classColor = 'background-fault-true';
      }else{
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        cov_threshold: 0,
        enable: false,
        history_enable: false,
        history_interval: 0,
        history_type: "INTERVAL",
        input_max: 0,
        input_min: 0,
        scale_min: 0,
        scale_max: 0,
        value_offset: 0,
        value_operation: 'ADD',
        value_round: 0,
        writable: false,
        tags: [{}],
        write_value: 0,
      }
      this.editField = "";
      this.tags = [];
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { 
      this.editedIndex = this.allPoints.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() { 
      this.editedIndex = -1;
      this.editedItem = {};
    },
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}

</style>